import None from "@/components/qy/none.vue" 
export default {
	data() {
		return {
			agentList:[],
			
			length:0,
			list_empty:false
		};
	},
	components: {None},
	onLoad() {
		this._load()
	},
	methods:{
		_load(){
			this.$api.http.get('fx/user/get_bind_user').then(res=>{
				this.agentList = res.data
			})
		}
	},
	onPullDownRefresh() {
		console.log('refresh');
		this._load();
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
